<template>
    <div class="product-view-container">
        <div class="products-view" @sumbit.prevent="getUser">
            đây là container của các sản phẩm giày
            {{posts}}
        </div>
    </div>
</template>

<script>
const axios = require("axios")
export default {
data(){
    return {
        posts:[]
    }
},
mounted(){
    axios
    .get('https://jsonplaceholder.typicode.com/posts')
    .then(Response => (this.posts = Response.data[1].title))
}
}
</script>

<style>
.product-view-container {
    width: 100%;
    height: auto;
    padding:20px;
}

.products-view {
    width: 100%;
    height: auto;
    margin: auto;
    flex-wrap: wrap;
    padding:10px;
    font-size: 40px;
}
</style>